import { printf, toText } from "../../fable_modules/fable-library.3.6.3/String.js";
import { Union } from "../../fable_modules/fable-library.3.6.3/Types.js";
import { union_type, string_type } from "../../fable_modules/fable-library.3.6.3/Reflection.js";

export class View extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Overview", "Account", "Transfer", "Invest", "Pensions", "Messages", "Profile", "DevSupport", "Accounts"];
    }
    toString() {
        const x = this;
        switch (x.tag) {
            case 8: {
                return "Accounts";
            }
            case 1: {
                return toText(printf("Account - %s"))(x.fields[0]);
            }
            case 2: {
                return "Payments \u0026 Transfer";
            }
            case 3: {
                return "Investment";
            }
            case 4: {
                return "Pension and Insurance";
            }
            case 5: {
                return "Messages";
            }
            case 6: {
                return "Profile";
            }
            case 7: {
                return "Developer Support";
            }
            default: {
                return "Overview";
            }
        }
    }
}

export function View$reflection() {
    return union_type("App.Components.Types.View", [], View, () => [[], [["Item", string_type]], [], [], [], [], [], [], []]);
}

export function View__get_IconName(x) {
    switch (x.tag) {
        case 8: {
            return "coins";
        }
        case 1: {
            return "";
        }
        case 2: {
            return "arrows";
        }
        case 3: {
            return "chart";
        }
        case 4: {
            return "wineglass";
        }
        case 5: {
            return "envelope";
        }
        case 6: {
            return "profile";
        }
        case 7: {
            return "code";
        }
        default: {
            return "lamp";
        }
    }
}

